import React, { Component } from 'react'

export class PredictionArea extends Component {

    constructor(props) {
        super(props)
    }

    render() {
        if(this.props.isPredicted){
            return (
                <div>
                    <p>
                        {this.props.data.prob} %
                    </p>
                    <p>
                        {this.props.data.ans} 
                    </p>
                </div>
            )
        }else{
            return(
                <div>
                    <p>PREDICT</p>
                </div>
            )
        }
    }
}

export default PredictionArea;